import React from "react"
import { Link } from "react-router-dom";

export default class EmbedViewerOnShopify extends React.Component {
    render() {
        return (
            <div className='help-page'>
                <h1>Integrating the viewer with your Shopify site</h1>
                <h2>Add the viewer to a product page</h2>
                <h4>1. Add a custom  metafield to link a Shopify product to an Ezyvue project</h4>
                <ol>
                    <li>On your store homepage go to the Settings page and select Metafields in the left column. In the Metafields choose Product.</li>
                    <img alt='create metafield' src={require('assets/images/help/shopify-metafields.jpg')} />
                    <li>Click the Add definition button, enter the text ezyvueproductid in the name field and Single text for the Content type then hit Save.</li>
                    <img alt='edit field' src={require('assets/images/help/shopify-metafield-def.jpg')} />
                    <li>Navigate to the Manage Project page on Ezyvue and copy the product id</li>
                    <img alt='copy product id' src={require('assets/images/help/shopify-poduct-id.jpg')} />
                    <li>On the Shopify product page paste the value into metafield field created earlier</li>
                    <img alt='paste id' src={require('assets/images/help/shopify-paste-id.jpg')} />
                </ol>
                <h4>2. Add a Liquid section to the product template page</h4>
                <p>
                    On your project template page add a Custom Liquid section where the viewer is to be displayed.
                    <img alt='paste id' src={require('assets/images/help/shopify-add-liquid.jpg')} />
                </p>
                <h4>3. Add condition code</h4>
                <p>
                    The viewer should be hidden for products that are not linked to an Ezyvue project.<br />
                    Add the following lines of code
                    <pre>{`
{% if product.metafields.custom.ezyvueproductid %}

{% endif %}
                        `}</pre>
                </p>
                <h4>4. Add the viewer iframe code</h4>
                <ol>
                    <li>Navigate to the <Link to='/embed'>integrate viewer page</Link> and choose the required settings.</li>
                    <li>In the HTML code section set the desired width and aspect ratio of the viewer window.</li>
                    <li>Leave the poject blank in the dropdown.</li>
                    <img alt='paste id' src={require('assets/images/help/shopify-ezyvue-embed.jpg')} />
                    <li>Click the copy button and paste the code into the Custom Liquid textbox.</li>
                    <img alt='paste id' src={require('assets/images/help/shopify-paste-code.jpg')} />
                    <li>Change the &#60;&#60;Replace with product id&#62;&#62; to &#123;&#123;product.metafields.custom.ezyvueproductid&#125;&#125;</li>
                </ol>
                The code should look similar to the following:
                <pre>{`
{% if product.metafields.custom.ezyvueproductid %}
    <div style="position: relative;padding-top:66.66%;">
        <div style="position:absolute;left:0;right:0;top:0;bottom:0">
            <iframe id="ezyvue-viewer" src="https://www.ezyvue.com/viewer?productId={{product.metafields.custom.ezyvueproductid}}
                &playbutton&animateplaybutton&playbuttontype=3d
                &enablear&showbackground=true&backgroundsize=contain" 
                width="100%" height="100%" style="border:none; 
                "frameborder="0" xr-spatial-tracking="true" 
                autoplay allowfullscreen mozallowfullscreen="true" 
                webkitallowfullscreen="true" allow="fullscreen">
            </iframe>
        </div>
    </div>
{% endif %}
`}</pre>
                <p>
                    You should now see your 3d model appear in the Custom Liquid section.<br />
                    <img alt='paste id' src={require('assets/images/help/shopify-final.jpg')} />
                </p>
                <h3>
                    Creating a 2-column section
                </h3>
                <p>
                    The following example shows how to split the section into two columns and display some content in the right column.
                </p>
                <pre>{`
<style>
    .one-half-column-left {
        width:48%;
        float:left;
    }

    .one-half-column-right {
        width:48%;
        float:right;
    }

    @media only screen and (max-width: 600px) {
    .one-half-column-left, .one-half-column-right, .one-third-column, .one-third-column-last {
        float:none;
        width:100%;
        margin-right:0;
    }
}
</style>
{% if product.metafields.custom.ezyvueproductid %}
    <div class="one-half-column-left">
        <div style="position: relative;padding-top:100%;">
            <div style="position:absolute;left:0;right:0;top:0;bottom:0">
                <iframe id="ezyvue-viewer" src="https://www.ezyvue.com/viewer?productId={{product.metafields.custom.ezyvueproductid}}
                    &playbutton&animateplaybutton&playbuttontype=3d
                    &enablear&showbackground=true&backgroundsize=contain" 
                    width="100%" height="100%" style="border:none; 
                    "frameborder="0" xr-spatial-tracking="true" 
                    autoplay allowfullscreen mozallowfullscreen="true" 
                    webkitallowfullscreen="true" allow="fullscreen">
                </iframe>
            </div>
        </div>
    </div>
    <div class="one-half-column-right">
        Place your content here
    </div>
    <br style="clear:both;" />
{% endif %}

                `}</pre>
                <h2>Add the viewer for a specific product</h2>
                <p>
                    Follow this <Link to='/help/embedViewer'>guide</Link> and paste the iframe HTML code into a Custom Liquid section.
                </p>
            </div>
        );
    }
}

